.navigation{
    background-color: rgb(36, 36, 36);
    width: 90vw;
    margin: 2vh 5vw;
    border-radius: 10px;
    position: fixed;
    height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    transition: all 0.5s;
    z-index: 2;
}

.navigation--container--logo{
    width: 40%;
    font-size: 30px;
    text-align: center;
    display: flex;
    padding-left: 2%;
    align-items: flex-start;
    font-weight: 1000;
    text-transform: uppercase;
    border-radius: 25px;
    color: white
}

.navigation--items{
    width: 60%;
    justify-content: center;
    align-items: center;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    color: white;
    list-style-type: none;
}

.hidden-navbar{
    transform: translateY(-20vh);
}
.navigation--items li a{
    text-decoration: none;
    color: white;
    padding: 3px;
    border-radius: 5px;
}

.navigation--items li a:hover{
    background-color: azure;
    color: black;
}

.navigation--container--logo a{
    text-decoration: none;
    color: white;
}


a:active{
    color: aquamarine!important;
}


/* Media query for screens smaller than 600px */
@media (max-width: 600px) {
    .navigation {
        flex-direction: column;
        height: auto;
        position: relative
    }

    .navigation--container--logo,
    .navigation--items {
        width: 100%;
    }

    .navigation--container--logo {
        padding: 10px;
        text-align: center;
    }

    .navigation--items {
        display: block;
        padding: 20px;
        line-height: 50px;
    }

}